body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.dashboard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 59.375rem;
}
.dashboard > .detalhes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
@media only screen and (max-width: 70em) {
  .dashboard {
    max-width: 29.375rem;
  }
}
@media only screen and (max-width: 40em) {
  .dashboard {
    max-width: 12.5rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
}
@media only screen and (max-width: 40em) {
  .header {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header > .left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 20.2rem;
}
@media only screen and (max-width: 40em) {
  .header > .left {
    min-width: 0;
  }
}
.header > .left > .titulo {
  font-size: var(--fz-titulo-dashboard-header);
  color: var(--clr-titulo-dashboard-header);
}
@media only screen and (max-width: 70em) {
  .header > .left > .titulo {
    --fz-titulo-dashboard-header: 1.08rem;
  }
}
.header > .left > .subtitulo {
  font-size: var(--fz-subtitulo-dashboard-header);
  color: var(--clr-subtitulo-dashboard-header);
  padding-top: 1rem;
}
@media only screen and (max-width: 70em) {
  .header > .left > .subtitulo {
    padding-top: 0.3rem;
    --fz-subtitulo-dashboard-header: 0.625rem;
  }
}
.header > .right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  min-width: 8.75rem;
}
@media only screen and (max-width: 40em) {
  .header > .right {
    width: 100%;
    align-items: flex-start;
    margin-top: 1rem;
    padding-top: 0.4rem;
    border-top: 1px solid var(--clr-lighttheme-50);
  }
}

.main {
  display: grid;
  width: 100%;
  margin-top: 3.125rem;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
  gap: 2rem;
}
.main > * {
  width: 100%;
}
@media only screen and (min-width: 40em) and (max-width: 70em) {
  .main {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media only screen and (max-width: 40em) {
  .main {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
}

.assinatura {
  font-size: 11px;
  text-align: center;
}
.assinatura > a {
  color: #3e52a3;
}

body > .background {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 30%;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: var(--clr-background-dashboard);
}